@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #111111;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Open Sans', sans-serif;
}

a {
  text-decoration: none;
  color: #111111;
}

.ch-wrapper {
  height: 100vh;
  position: relative;
}

.ch-wrapper:before {
  content: '';
  width: 62.4%;
  left: -15%;
  top: 0;
  display: block;
  position: absolute;
  height: 100%;
  background: #FF851B;
  transform: skew(-30deg);
}

.ch-container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.ch-header {
  padding: 30px 50px;
  position: relative;
  z-index: 2;
}

.ch-header--logo {
  padding-right: 25px;
  color: #fff;
  font-size: 22px;
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
}

.ch-header--logo:after {
  content: '';
  display: inline-block;
  height: 15px;
  width: 1px;
  background: #fff;
  margin-left: 35px;
}

.ch-header--nav__list {
  list-style-type: none;
}

.ch-header--nav__list li {
  margin-right: 30px;
  font-size: 14px;
  font-weight: 600;
}

.ch-header--nav__list li a {
  color: #fff;
}

.ch-header--links svg {
  margin-left: 10px;
}

.ch-header--cta {
  display: inline-block;
  padding: 0.6rem 1.8rem;
  font-size: 12px;
  font-weight: 600;
  background: #111111;
  border-radius: 25px;
  margin-left: 15px;
  color: #fff;
}

.ch-hero {
  height: 500px;
  background: pink;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 60px 80px 40px;
  overflow: hidden;
  position: relative;
  background-image: url(https://images.unsplash.com/photo-1475938476802-32a7e851dad1?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=c7c3b42964cf6b52784c8c1a4e73fa97&auto=format&fit=crop&w=1050&q=80);
}

.ch-hero:after {
  content: '';
  display: block;
  width: 60%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -13%;
  background: rgba(0, 0, 0, 0.5);
  transform: skew(-30deg);
}

.ch-hero--content, .ch-hero--bottom {
  z-index: 1;
  position: relative;
}

.ch-hero--title {
  font-size: 35px;
  max-width: 40%;
  color: #fff;
  padding-right: 25px;
  margin-bottom: 35px;
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
}

.ch-hero--control {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border-radius: 50%;
  text-align: center;
  line-height: 38px;
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  margin-right: 15px;
}

.ch-hero--author {
  text-align: right;
  font-weight: 600;
  font-size: 12px;
  color: #fff;
}

.ch-hero--author__name {
  font-size: 14px;
}

.ch-hero--author__icon {
  margin-left: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #7FDBFF;
  color: #fff;
  text-align: center;
  line-height: 50px;
}

.ch-hero--cta {
  display: inline-block;
  padding: 0.6rem 2rem;
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 25px;
  font-size: 12px;
  font-weight: 600;
}

.ch-hero--trial {
  font-size: 14px;
  background: #FF4136;
  border: 1px solid #FF4136;
  margin-right: 25px;
  padding: 0.7rem 2rem;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-direction--column {
  flex-direction: column;
}
